export default ()=> {
      return (
        <div className="question" data-index="37" dangerouslySetInnerHTML = {{ __html: `
<h6>37. 输出是什么？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> numbers <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token number">1</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">,</span> <span class="token number">3</span><span class="token punctuation">]</span>
numbers<span class="token punctuation">[</span><span class="token number">10</span><span class="token punctuation">]</span> <span class="token operator">=</span> <span class="token number">11</span>
console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>numbers<span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>[1, 2, 3, 7 x null, 11]</code></li>
<li>B: <code>[1, 2, 3, 11]</code></li>
<li>C: <code>[1, 2, 3, 7 x empty, 11]</code></li>
<li>D: <code>SyntaxError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: C</h4>
<p>当你为数组设置超过数组长度的值的时候， JavaScript 会创建名为 &quot;empty slots&quot; 的东西。它们的值实际上是 <code>undefined</code>。你会看到以下场景：</p>
<p><code>[1, 2, 3, 7 x empty, 11]</code></p>
<p>这取决于你的运行环境（每个浏览器，以及 node 环境，都有可能不同）</p>
</p>
</details>
` }}></div>
      );
    }